<template>
  <div class="search-keyword">
    <v-container>
      <v-row>
        <v-col class="d-flex justify-center">
          <v-text-field
            width="85%"
            v-model="searchQueryKeyword"
            label=""
            single-line
            hide-details
            class="mx-auto mb-2"
            variant="outlined"
            append-inner-icon="mdi-magnify"
            rounded
          ></v-text-field>
          <v-btn
            @click="resetSearch"
            class="rounded-lg ml-2"
            variant="tonal"
            size="x-large"
          ></v-btn>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <h4 class="pl-7">  : <span>      .</span></h4>
          <v-chip-group v-model="selectedTags" multiple class="mb-10">
            <v-chip v-for="tag in tags" :key="tag" class="ma-2" @click="addSearchQuery(tag)">
              {{ tag }}  
            </v-chip>
          </v-chip-group>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-card width="100%" class="mx-auto pa-0 rounded-lg" variant="outlined">
            <v-list lines="two">
              <v-list-item
                v-for="item in paginatedItems"
                :key="item.id"
                @click="openModal(item)"
              >
                <template v-slot:prepend>
                  <v-avatar color="amber">
                    <v-icon color="white">mdi-clipboard-text</v-icon>
                  </v-avatar>
                </template>

                <template v-slot:default>
                  <v-list-item-content class="d-flex flex-column">
                    <v-list-item-title class="font-weight-bold" style="font-size: 16px;">
                      {{ item.title.replace(/<\/?[^>]+(>|$)/g, "") }}
                    </v-list-item-title>
                    <v-list-item-subtitle class="text-left hidden-md-and-down" style="font-size: 16px;">
                      {{ (item.body.length > 30 ? item.body.substring(0, 50) + '...' : item.body).replace(/<\/?[^>]+(>|$)/g, "") }}
                    </v-list-item-subtitle>
                    <v-list-item-subtitle class="text-left hidden-md-and-up" style="font-size: 16px;">
                      {{ (item.body.length > 30 ? item.body.substring(0, 20) + '...' : item.body).replace(/<\/?[^>]+(>|$)/g, "") }}
                    </v-list-item-subtitle>
                  </v-list-item-content>
                </template>
                
                <template v-slot:append>
                  <v-list-item-subtitle>{{ item.createAt.toDate().toLocaleDateString() }}</v-list-item-subtitle>                  
                </template>
              </v-list-item>
            </v-list>            
          </v-card>          
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-pagination
            v-model="page"
            :length="totalPages"
          ></v-pagination>
        </v-col>
      </v-row>
    </v-container>
    <v-dialog v-model="modalOpen" width="100%">
      <v-card class="overflow-y-auto rounded-lg">
        <v-card-title class="my-3 text-center">
          <input v-model="title" class="text-center w-100 editable" height="auto" style="font-weight: bold; font-size: 30px;">
        </v-card-title>
        <v-card-subtitle class="text-right mb-10">
          {{ selectedItem.createAt.toDate().toLocaleDateString() }}
        </v-card-subtitle>
        <v-card-text>
          <!-- <textarea rows="15" v-model="body" class="w-100 editable"></textarea>             -->
          <textarea :rows="Math.max(15, body.split('\n').length)" v-model="body" class="w-100 editable"></textarea>
        </v-card-text>
        <v-card-actions class="mt-15 d-flex flex-column justify-center">
            <div class="mb-5">
              <v-btn
                size="small"
                color="primary"
                icon="mdi-pencil"
                @click.stop="editItem(item)"
              ></v-btn>
              <v-btn
                class="mx-3"
                size="small"
                color="error"
                icon="mdi-delete"
                @click.stop="deleteItem()"
              ></v-btn>          
              <v-btn
                size="small"
                color="grey"
                icon="mdi-cancel"
                @click.stop="cancelEdit(item)"
              ></v-btn>
            </div>
            <!-- <div>
              <v-btn color="primary" variant="outlined" @click="modalOpen = false"></v-btn>
            </div> -->
          </v-card-actions>          
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { ref, computed, onMounted } from 'vue';
import { db, auth } from '../firebase'; // Adjust the path according to your project structure
import { collection, query, where, getDocs, doc, updateDoc, deleteDoc, orderBy } from 'firebase/firestore';
import { onAuthStateChanged } from 'firebase/auth';

export default {
  name: 'SearchKeyword',
  setup() {
    const studyItems = ref([]);
    const searchQueryKeyword = ref('');
    const modalOpen = ref(false);
    const selectedItem = ref({});
    const userEmail = ref('');
    const page = ref(1);
    const itemsPerPage = 10;
    const title = ref('');
    const body = ref('');
    const tags = ref([]);
    const selectedTags = ref([]);


    //     
    async function fetchTags(email) {
      try {
        console.log('Fetching tags for email:', email); //   
        const postsQuery = query(collection(db, 'posts'), where('author', '==', email));
        const querySnapshot = await getDocs(postsQuery);

        const tagsSet = new Set(); //    Set

        querySnapshot.forEach(doc => {
          const postTags = doc.data().tags; //    
          if (postTags && Array.isArray(postTags)) {
            postTags.forEach(tag => tagsSet.add(tag)); // Set  
          }
        });

        tags.value = Array.from(tagsSet); // Set   
        console.log('Fetched tags:', tags.value); //   

      } catch (error) {
        console.error('Error fetching tags:', error);
      }
    }

    //   
    async function fetchStudyItems(email) {
      //  
      title.value = '';
      body.value = '';

      const q = query(
        collection(db, 'posts'),
        where('author', '==', email),
        orderBy('createAt', 'desc') // 'createAt'    
      );
      const querySnapshot = await getDocs(q);
      studyItems.value = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
    }

    //        
    function openModal(item) {
      selectedItem.value = item;
      title.value = item.title; //    title ref 
      body.value = item.body; //    body ref 
      modalOpen.value = true;
    }

    //    
    async function editItem() {
      const postDoc = doc(db, 'posts', selectedItem.value.id);
      await updateDoc(postDoc, {
        title: title.value, // title ref   
        body: body.value // body ref   
      });
      fetchStudyItems(userEmail.value); //    
      modalOpen.value = false; //  
    }

    //    
    async function deleteItem() {
      const item = selectedItem.value;
      if (!item || !item.id) {
        console.error("   ID .");
        return;
      }
      const postDoc = doc(db, 'posts', item.id);
      await deleteDoc(postDoc);
      fetchStudyItems(userEmail.value); //    
      modalOpen.value = false; //  
    }

    //    
    function cancelEdit() {
      modalOpen.value = false;
    }

    //      
    const filteredStudyItems = computed(() => {
      if (searchQueryKeyword.value) {
        const searchTerms = searchQueryKeyword.value.toLowerCase().split(/\s*,\s*|\s+/);
        return studyItems.value.filter(item => {
          return searchTerms.some(term =>
            item.title.toLowerCase().includes(term) ||
            item.body.toLowerCase().includes(term) || 
            (Array.isArray(item.tags) && item.tags.some(tag => tag.toLowerCase().includes(term)))
          );
        });
      }
      return studyItems.value;
    });

    // Paginate filtered items
    const paginatedItems = computed(() => {
      const start = (page.value - 1) * itemsPerPage;
      const end = start + itemsPerPage;
      const items = filteredStudyItems.value.slice(start, end);
      return items;
    });

    // Calculate total pages for pagination
    const totalPages = computed(() => {
      return Math.ceil(filteredStudyItems.value.length / itemsPerPage);
    });

    //      
    const addSearchQuery = (tag) => {
      searchQueryKeyword.value += (searchQueryKeyword.value ? ', ' : '') + tag;
    };

    //    
    function resetSearch() {
      searchQueryKeyword.value = ''; //     
    }

    //        
    onMounted(() => {
      onAuthStateChanged(auth, (user) => {
        if (user) {
          userEmail.value = user.email;
          fetchStudyItems(user.email);
          fetchTags(user.email);
        } else {
          //     
        }
      });
    });

    return {
      studyItems,
      searchQueryKeyword,
      modalOpen,
      selectedItem,
      filteredStudyItems,
      paginatedItems,
      page,
      totalPages,
      title,
      body,
      tags,
      selectedTags,
      resetSearch,
      openModal,
      editItem,
      deleteItem,
      cancelEdit,
      addSearchQuery
    };
  }
}
</script>

<style scoped>  
  .search-keyword h4 span{
    font-size: 14px;
    color: #8e8e8e;
    padding-left: 10px;
  }
  .search-keyword .v-chip-group{
    width: 96%; height:auto; margin: auto;
  }
  .search-keyword .v-card{
    padding: 50px 20px !important;
  }
  .search-keyword .v-pagination{
    margin-bottom: 100px !important;
  }
</style>
