<template>
  <component :is="layout">
    <router-view></router-view>
  </component>  
</template>

<script>
import { auth, db } from '@/firebase';
import { ref, onMounted, onBeforeUnmount } from 'vue';
import { useRouter } from 'vue-router';
import { onAuthStateChanged, signOut } from 'firebase/auth';
import { doc, getDoc } from 'firebase/firestore';
import DefaultLayout from '@/layouts/DefaultLayout'
import IntroLayout from '@/layouts/IntroLayout'

export default {
  name: 'App',
  data: () => ({ 
    open: [],    
  }),
  computed: {
    layout() {
      return (this.$route.meta && this.$route.meta.layout) || 'DefaultLayout'
    }
  },
  components: {
    DefaultLayout,
    IntroLayout
  },
  setup() {
    const router = useRouter();
    const user = ref(null);
    const isLoggedIn = ref(false);
    const drawer = ref(false);
    const role = ref('');

    const links = ref([
      { name: '', path: '/home',  icon: 'mdi-home' },
      { name: '', path: '/about', icon: 'mdi-account' },
      { name: '', path: '/study', icon: 'mdi-book-open' },
      { name: '', path: '/search', icon: 'mdi-magnify' },
      { name: '', path: '/manual', icon: 'mdi-help' },
    ]);
    const menus = ref([
      { name: '', path: '/home', active: false },
      { name: '', path: '/about', active: false },
      { name: '', path: '/study', active: false },
      { name: '', path: '/search', active: false },
      { name: '', path: '/manual', active: false },
    ]);

    /**
     * Firebase      
     *
     * 1. Firebase       /  .
     * 2.   `user.value`    `isLoggedIn.value` true .
     * 3.   `user.value` null  `isLoggedIn.value` false .
     * 4.        .
     */

    const fetchUserDetails = async (uid) => {
      const userDoc = await getDoc(doc(db, 'users', uid));
      if (userDoc.exists()) {
        const userData = userDoc.data();
        role.value = userData.role; //  
        user.value.name = userData.name; //  
      } else {
        role.value = 'user'; //   
        user.value.name = ''; //   
      }
    };


    const initFirebaseAuth = () => {
      const unsubscribe = onAuthStateChanged(auth, async (firebaseUser) => {
        if (firebaseUser) {
          user.value = firebaseUser;
          isLoggedIn.value = true;
          await fetchUserDetails(firebaseUser.uid);
        } else {
          user.value = null;
          isLoggedIn.value = false;
          role.value = null;
        }
      });

      onBeforeUnmount(unsubscribe);
    };

    //  : auth  
    /**
     *    
     *
     * 1. Firebase      .
     * 2.    `user.value` null  `isLoggedIn.value` false .
     * 3.    '/home'  .
     * 4.       .
     */
    const handleLogout = async () => {
      try {
        await signOut(auth);
        user.value = null;
        isLoggedIn.value = false;
        router.push('/');
      } catch (error) {
        console.error(' :', error);
      }
    };

    onMounted(() => {
      initFirebaseAuth();
    });

    return { drawer, links, menus, isLoggedIn, user, role, initFirebaseAuth, handleLogout };
  }
}
</script>

<style>
/*    */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* App */
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;  
  height: 100vh;
}
</style>
